@import "https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: League Spartan, sans-serif;
}

:root {
  --background-color: #e3e6f3;
  --first-color: #088178;
  --brand-color: #898a8f;
  --star-color: #ddcf4f;
}

h1 {
  color: #000;
  font-size: 48px;
  line-height: 60px;
}

h2 {
  color: #000;
  font-size: 44px;
  line-height: 50px;
}

h4 {
  color: #000;
  font-size: 20px;
}

h6 {
  font-size: 16px;
  font-weight: 700;
}

p {
  color: #000;
  margin: 16px 0 20px;
  font-size: 16px;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  padding: 40px 0;
}

button.normal {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
}

button.white {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 4px;
  outline: none;
  padding: 12px 20px;
  font-size: 12px;
  font-weight: 600;
  transition: all .3s;
}

body {
  width: 100%;
}

.logo {
  width: 50%;
  height: 50%;
}

#header {
  background-color: var(--background-color);
  z-index: 999;
  justify-content: space-between;
  align-items: center;
  padding: 8px 80px;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 4px 16px #0000000f;
}

#navbar {
  justify-content: center;
  align-items: center;
  display: flex;
}

#navbar li {
  padding: 0 20px;
  list-style: none;
  position: relative;
}

#navbar li a {
  color: #000;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
}

#navbar li a:hover, #navbar li a.active {
  color: var(--first-color);
}

#navbar li:nth-child(-n+5) a.active:after, #navbar li:nth-child(-n+5) a:hover:after {
  content: "";
  background: var(--first-color);
  width: 36%;
  height: 2.4px;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

#mobile {
  align-items: center;
  display: none;
}

#close {
  display: none;
}

#hero {
  background-image: url("hero4.307705a5.png");
  background-position: right 0% top 25%;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 90vh;
  padding: 0 80px;
  display: flex;
}

#hero h4 {
  padding-bottom: 16px;
}

#hero h1 {
  color: var(--first-color);
}

#hero button {
  color: var(--first-color);
  cursor: pointer;
  background-color: #0000;
  background-image: url("button.52502b1b.png");
  background-repeat: no-repeat;
  border: 0;
  padding: 14px 80px 14px 65px;
  font-size: 16px;
  font-weight: 700;
}

#feature {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#feature .feature-box {
  text-align: center;
  border: 1px solid var(--first-color);
  border-radius: 4px;
  width: 180px;
  margin: 16px 0;
  padding: 24px 16px;
  box-shadow: 20px 20px 32px #0000003d;
}

#feature .feature-box:hover {
  box-shadow: 10px 10px 56px #00000052;
}

#feature .feature-box img {
  width: 100%;
  margin-bottom: 12px;
}

#feature .feature-box h6 {
  color: var(--first-color);
  border-radius: 4px;
  padding: 12px 8px 4px;
  line-height: 1;
  display: inline-block;
}

#product-1 {
  text-align: center;
}

#product-1 .prod-container {
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
  display: flex;
}

#product-1 .prod {
  border: 1px solid var(--background-color);
  cursor: pointer;
  border-radius: 28px;
  width: 23%;
  min-width: 256px;
  margin: 16px 0;
  padding: 12px 16px;
  position: relative;
  box-shadow: 20px 20px 32px #0000001f;
}

#product-1 .prod:hover {
  transition: all .3s;
  box-shadow: 20px 20px 32px #0003;
}

#product-1 .prod img {
  border-radius: 24px;
  width: 100%;
}

#product-1 .prod .description {
  text-align: start;
  padding: 12px 0;
}

#product-1 .prod .description span {
  color: var(--brand-color);
  font-size: 12px;
}

#product-1 .prod .description h5 {
  padding-top: 8px;
  font-size: 16px;
}

#product-1 .prod .description i {
  color: var(--star-color);
  font-size: 16px;
}

#product-1 .prod .description h4 {
  color: var(--first-color);
  padding-top: 8px;
  font-size: 16px;
  font-weight: 700;
}

#product-1 .prod .cart {
  background-color: var(--background-color);
  color: var(--first-color);
  border: 1px solid #d4cccc;
  border-radius: 56px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  bottom: 20px;
  right: 12px;
}

#banner {
  text-align: center;
  background-image: url("b2.9e341162.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;
  display: flex;
}

#banner h4 {
  color: #fff;
  font-size: 16px;
}

#banner h2 {
  color: #fff;
  padding: 12px 0;
  font-size: 32px;
}

#banner h2 span {
  color: red;
  padding: 12px 0;
  font-size: 32px;
}

#banner button:hover {
  background-color: var(--first-color);
  color: #fff;
}

#sm-banner {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

#sm-banner .banner-box {
  background-image: url("b17.f58f5be2.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 560px;
  height: 50vh;
  padding: 32px;
  display: flex;
}

#sm-banner h4 {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
}

#sm-banner h2 {
  color: #fff;
  font-size: 28px;
  font-weight: 800;
}

#sm-banner span {
  color: #fff;
  padding-bottom: 16px;
  font-size: 14px;
  font-weight: 500;
}

#sm-banner .banner-box:hover button {
  background: var(--first-color);
  border: 1px solid var(--background-color);
}

#sm-banner .banner-box:nth-child(2) {
  background-image: url("b10.07703842.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 560px;
  height: 50vh;
  padding: 32px;
  display: flex;
}

#banner-3 {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 80px;
  display: flex;
}

#banner-3 .banner-box {
  background-image: url("b7.916e022a.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 30%;
  height: 30vh;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

#banner-3 h2 {
  color: #fff;
  font-size: 32px;
  font-weight: 600;
}

#banner-3 h3 {
  color: red;
  font-size: 24px;
  font-weight: 800;
}

#banner-3 .banner-box:nth-child(2) {
  background-image: url("b4.d38a91c4.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 30%;
  height: 30vh;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

#banner-3 .banner-box:nth-child(3) {
  background-image: url("b18.c58ca4bf.jpg");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 30%;
  height: 30vh;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

#newsletter {
  background-color: #041e42;
  background-image: url("b14.b4ea5e45.png");
  background-position: 20% 30%;
  background-repeat: no-repeat;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0;
  display: flex;
}

#newsletter h4 {
  color: #fff;
  font-size: 24px;
  font-weight: 700;
}

#newsletter p {
  color: var(--brand-color);
  font-size: 16px;
  font-weight: 600;
}

#newsletter p span {
  color: var(--star-color);
}

#newsletter .form {
  width: 40%;
  display: flex;
}

#newsletter input {
  border: 1px solid #0000;
  border-radius: 4px 0 0 4px;
  outline: none;
  width: 100%;
  height: 3.2rem;
  padding: 0 1.2rem;
  font-size: 16px;
}

#newsletter button {
  background-color: var(--first-color);
  color: #fff;
  white-space: nowrap;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

footer {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

footer .col {
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
  display: flex;
}

footer .logo1 {
  margin-bottom: 32px;
}

footer h4 {
  padding-bottom: 20px;
  font-size: 16px;
}

footer p {
  margin: 0 0 8px;
  font-size: 12px;
}

footer a {
  color: #222;
  margin-bottom: 12px;
  font-size: 12px;
  text-decoration: none;
}

footer .follow {
  margin-top: 20px;
}

footer .follow i {
  color: var(--brand-color);
  cursor: pointer;
  padding-right: 4px;
}

footer .install .row img {
  border: 1px solid var(--first-color);
  border-radius: 4px;
}

footer .install img {
  margin: 12px 0 16px;
}

footer .follow i:hover, footer a:hover {
  color: var(--first-color);
}

footer .copyright {
  text-align: center;
  width: 100%;
}

@media (width <= 800px) {
  .section-p1 {
    padding: 40px;
  }

  #navbar {
    background-color: var(--background-color);
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 300px;
    height: 100vh;
    padding: 80px 0 0 12px;
    transition: all .3s;
    display: flex;
    position: fixed;
    top: 0;
    right: -300px;
    box-shadow: 0 40px 56px #0000001a;
  }

  #navbar.active {
    right: 0;
  }

  #navbar li {
    margin-bottom: 24px;
  }

  #mobile {
    align-items: center;
    display: flex;
  }

  #mobile i {
    color: #000;
    padding-left: 20px;
    font-size: 24px;
  }

  #close {
    display: initial;
    color: #000;
    font-size: 24px;
    position: absolute;
    top: 32px;
    left: 32px;
  }

  #lg-bag {
    display: none;
  }

  #hero {
    background-position: right 30% top 30%;
    height: 90vh;
    padding: 0 80px;
  }

  #feature {
    justify-content: center;
  }

  #feature .feature-box {
    margin: 16px;
  }

  #product-1 .prod-container {
    justify-content: center;
  }

  #product-1 .prod {
    margin: 16px;
  }

  #banner {
    height: 20vh;
  }

  #sm-banner .banner-box, #sm-banner .banner-box:nth-child(2) {
    min-width: 100%;
    height: 30vh;
    padding: 32px;
  }

  #banner-3 {
    padding: 0 40px;
  }

  #banner-3 .banner-box {
    width: 28%;
  }

  #newsletter .form {
    width: 70%;
  }
}

@media (width <= 480px) {
  .section-p1 {
    padding: 20px;
  }

  #header {
    padding: 12px 28px;
  }

  h1 {
    font-size: 36px;
  }

  h2 {
    font-size: 32px;
  }

  #hero {
    background-position: 55%;
    padding: 0 20px;
  }

  #feature {
    justify-content: space-between;
  }

  #feature .feature-box {
    width: 156px;
    margin: 0 0 16px;
  }

  #product-1 .prod {
    width: 100%;
  }

  #banner, #sm-banner .banner-box {
    height: 40vh;
  }

  #sm-banner .banner-box:nth-child(2) {
    margin-top: 20px;
  }

  #banner-3 {
    padding: 0 20px;
  }

  #banner-3 .banner-box {
    width: 100%;
  }

  #newsletter {
    padding: 40px 20px;
  }

  #newsletter .form {
    width: 100%;
  }
}

